import { Api } from '../base/api';
export class ApiModel extends Api {
    constructor(baseUrl, options) {
        super(baseUrl, options);
    }
    // получаем массив объектов(карточек) с сервера
    getListProductCard() {
        return this.get('/product').then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: 'https://larek-api.nomoreparties.co/content/weblarek' + item.image }))));
    }
    // получаем ответ от сервера по сделанному заказу
    postOrderLot(order) {
        return this.post(`/order`, order).then((data) => data);
    }
}
